body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  padding: 1rem 10rem;
}

.class {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.spacing {
  margin-bottom: 1rem;
}

.center {
  position: relative;
  left: 20rem;
}

ul {
  list-style-type: none;
  font-size: 12pt;
  padding: 0;
}

.slide {
  text-align: center;
  width: 45rem;
  height: 15rem;
  line-height: 12rem;
}

.steps {
  list-style: none;
}

.steps li {
  margin: 2em 0;
  padding-left: 1em;
  padding-top: 1em;
  display: block;
  position: relative;
  counter-increment: inst;
}

.steps li::before {
  content: counter(inst);

  background: rgba(255, 150, 0, 0.35);
  color: #fff;

  font-size: 1em;
  font-weight: 700;
  font-style: italic;
  text-shadow: 1px 1px rgba(255, 150, 0, 0.5);

  border-radius: 0 0.675em 0.675em 0;
  font-size: 1.5em;
  text-align: center;

  padding-top: 0;
  padding-left: 2.25em;
  left: -2.2em;
  top: 0.25em;
  height: 1.35em;
  width: 1.35em;
  position: absolute;

}
@media (min-width: 33em) {
  .steps li:before {
    border-radius: 1em;
    font-size: 1.5em;
    margin-left: 0.5em;
    padding-left: 0;
    padding-top: 0;
    width: 1.35em;
  }
}


.background-image {
    background-image: url('https://images.unsplash.com/photo-1450152021501-598b36b17449?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60');
    background-size: cover;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 0;
    width:110em;
